import React from 'react';
import './App.module.css';
import s from './App.module.css';
import Logo from './components/img/Logo.png';
import BG from './components/img/BG.png';
import BigImg from './components/Home/img/BigImg.png';
import HomeItem1 from './components/Home/img/HomeItem1.png';
import HomeItem2 from './components/Home/img/HomeItem2.png';
import Header from './components/Header/Header';
import Home from './components/Home/Home'
import About from "./components/About/About";
import Services from "./components/Services/Services";
import Gallery from "./components/Gallery/Gallery";
import News from "./components/News/News";
import {BrowserRouter, Route} from "react-router-dom";


function App(props) {
    return (
        <BrowserRouter>
            <div className={s.BG}>
                <div className={s.contentLogo}>
                    <img className={s.logo} src={Logo} alt="sorry bro"></img>
                </div>
                <div className={s.content}>
                    <Header/>
                    <Route path="/" exact component={Home}/>
                    <Route path="/home" component={Home}/>
                    <Route path="/about" component={About}/>
                    <Route path="/services" component={Services}/>
                    <Route path="/gallery" component={Gallery}/>
                    <Route path="/news" component={News}/>
                </div>
            </div>
        </BrowserRouter>
    );
}

export default App;