import React from "react";
import s from "./Header.module.css";
import HomeItem1 from "../Home/img/HomeItem1.png";
import HomeItem2 from "../Home/img/HomeItem2.png";
import Home from "../Home/Home";
import About from "../About/About";
import {NavLink} from "react-router-dom";





const Header = (props) => {
    return (
        <div className={s.NavBar}>
            <div className={s.NavbarItems}>
                <nav></nav>
                <nav></nav>
                <nav></nav>
                <nav></nav>
                <nav></nav>
                <nav ><NavLink className={s.Items} activeClassName={s.activeLink} to="/home">HOME</NavLink></nav>
                <nav ><NavLink className={s.Items} activeClassName={s.activeLink} to="/about">ABOUT</NavLink></nav>
                <nav ><NavLink className={s.Items} activeClassName={s.activeLink} to="/services">SERVICES</NavLink></nav>
                <nav ><NavLink className={s.Items} activeClassName={s.activeLink} to="/gallery">GALLERY</NavLink></nav>
                <nav ><NavLink className={s.Items} activeClassName={s.activeLink} to="/news">NEWS</NavLink></nav>
            </div>
        </div>
    )
}


export default Header;